<template>
  <div >
   <!--查询form start-->
    <el-form class="thermal-row-formitem" label-position="left" :model="ruleForm" :rules="rules" ref="ruleForm2" label-width="130px"
             style="text-align: left;font-size:20px;font-weight: bold;" @submit.native.prevent>
      <el-card class="thermal-row-card1 thermal-row-card2" shadow="always" :body-style="cardBodyStyle" >
        <div slot="header" style="font-size: 14px;font-weight: bold; " @click="confCardBodyHeigth">
          <el-row  @click="confCardBodyHeigth">
            <el-col :md="23" >
              <span>角色信息</span>
            </el-col>
            <!--   查询框隐藏显示按钮         -->
            <el-col :md="1"   @click="confCardBodyHeigth">
              <el-link   :icon="icon1" :underline="false"  style="float: right;text-decoration: none;font-size:20px;height: 10px;line-height: 10px;" > </el-link>
            </el-col>
          </el-row>
        </div>
        <div v-show="cardBodyHeight">
          <el-row :gutter="24"  >
            <el-col :md="12" >
              <el-form-item  label="角色名称"   >
                <el-input v-model="ruleForm.r_caption" style="width: 300px"></el-input>
              </el-form-item>
            </el-col>
            <el-col :md="12" >
              <el-button style="float:right;" type="primary" @click="queryValveCurrentData">查询</el-button>
            </el-col>
          </el-row>
        </div>
      </el-card>
    </el-form >
   <!--form end-->

   <!--页面 start-->
    <el-card >
      <!-- 悬浮球组件start -->
        <div style="position:relative;" id="dragbtn" >
        <div v-drag draggable="false"
             :style="{ 'width':'62px',
                     'right':this.floatRightValue,
                     'top':this.floatTopValue,
                    'border-radius':'50%' ,
                    'position':'fixed',
                    'padding-left': '0px',
                    'padding-top': '8px',
                    'cursor': 'pointer',
                    'z-index': '888'}"
        >
          <div v-show="ballShow" :style="{display:'flex','justify-content':'space-between',position: 'absolute',left:this.menuLeft,'width':this.menuWidth, top:' 10px',}"
               id="buttonThermalUser" >
            <!--   第一个悬浮按钮         -->
            <!---->
            <div style="float:right;margin-right: 20px;">
              <el-button type="primary" size="small" title="新增数据" @click="platformAddRole"> <i class="el-icon-edit ">新增</i></el-button>
            </div>

          </div>

          <!--        </transition>-->
          <el-tooltip content="展开与隐藏功能按钮" placement="top">
            <el-button  v-trigger id="ballBtn" ref="ballBtn"  type="primary" icon="el-icon-close" :icon="this.ballIcon" circle @click.stop.prevent="openBox"
                        :class="[ballShow === true ? 'fadeIn': 'fadeOut']"></el-button>
          </el-tooltip>
        </div>
      </div>
      <!-- 悬浮球组件end -->

      <!--  页面表格 start   -->
        <el-table
        border
        ref="thermalRoleTable"
        :data="tableData"
        tooltip-effect="dark"
        @sort-change="sortColumn"
        :row-style="{'height':'10px'}"
        :cell-style="{'padding':'5px 0px'}"
        :header-cell-style="{'background': '#4B86BC','color':'#ffffff','borderRight': '1px solid #E3E5EB' ,'padding':'0px','height':'45px','line-height':'45px' }"
      >
<!--        <el-table-column
          type="selection"
          :selectable="isSelect"
          width="55">
        </el-table-column>-->
          <el-table-column
            fixed
            align="center"
            type="index"
            label="序号"
            width="50">
          </el-table-column>
        <el-table-column
          prop="r_caption"
          label="角色名称"
          align="center"
          show-overflow-tooltip
        >
        </el-table-column>
        <el-table-column
          prop="r_memo"
          label="备注"
          align="center"
          show-overflow-tooltip
          >
        </el-table-column>
        <el-table-column
          sortable
          prop="r_writetime"
          label="修改时间"
          align="center"
          show-overflow-tooltip
        >
        </el-table-column>
        <el-table-column
          label="操作"
          width="180"
          align="center"
        >
          <template slot-scope="scope">
            <el-button @click.native.stop="platformEditRole(scope.row)" type="text" size="small">编辑</el-button>
            <el-button @click.native.stop="platformDelRole(scope.row)" type="text" size="small">删除</el-button>
<!--            <el-button
              type="primary" icon="el-icon-document-add" circle
              size="mini"
              @click.native.stop="platformAddRole()"></el-button>-->
            <!-- <el-tooltip  content="编辑" placement="top">
              <el-button
                type="primary" icon="el-icon-edit" circle
                size="mini"
                @click.native.stop="platformEditRole(scope.row)"></el-button>
            </el-tooltip>
            <el-tooltip  content="删除" placement="top">
              <el-button
                type="danger"  icon="el-icon-delete" circle
                size="mini"
                @click.native.stop="platformDelRole(scope.row)"></el-button>
            </el-tooltip> -->
          </template>
        </el-table-column>
      </el-table>
      <!--  页面表格 end   -->
    </el-card>
   <!--页面 end-->

   <!--page start-->
    <el-pagination
      background
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="queryParam.currentPage"
      :page-sizes="[10, 20, 50, 100,200]"
      :page-size="queryParam.pageSize"
      layout="total, sizes, prev, pager, next, jumper"
      :total="queryParam.totalRow">
    </el-pagination>
   <!--page end-->

   <!--  弹出窗口  -->
    <el-dialog :visible.sync="thermalRoleFormVisible"
               :close-on-press-escape="false"
               :close-on-click-modal="false"
               :show-close="false"
               class="thermal-row-dialog"

    >
      <div slot="title"  style="text-align: left;font-size:20px;font-weight: bold;">
        <span  >{{this.tableDialgTitle}}</span>
      </div>
      <div>
        <el-card class="thermal-row-card1 thermal-row-card2" style="background-color: #d6d6d6">
          <el-row>
            <el-col :span="12"><span style="font-size: 16px">角色信息</span></el-col>
            <el-col :span="12"><span style="font-size: 16px">权限信息</span>({{this.menuNum}})</el-col>
          </el-row>
        </el-card>
        <el-card>
          <el-container >
            <el-aside style="width:50%;background-color:white">
              <!--form start-->
              <el-form class="thermal-row-formitem" label-position="top"  :model="thermalRoleForm" :rules="permissionFormRules" ref="thermalRoleForm1" label-width="130px"
                       style="text-align: left;font-size:20px;font-weight: bold;margin-top: 60px">
                <el-form-item  label="角色名称" prop="r_caption"  :rules="this.permissionFormRules.caption">
                  <el-input v-model="thermalRoleForm.r_caption" style="width: 80%"></el-input>
                </el-form-item>

                <el-form-item  label="备注" prop="r_memo"  :rules="this.permissionFormRules.memo">
                  <el-input v-model="thermalRoleForm.r_memo" style="width: 80%"></el-input>
                </el-form-item>
                <el-form-item v-show="false" label="ID" prop="r_id" >
                  <el-input v-model="thermalRoleForm.r_id"></el-input>
                </el-form-item>
              </el-form>
              <!--form end-->
            </el-aside>
            <el-container>
              <el-table
                height=400
                :data="tableTreeData"
                style="width: 100%;margin-bottom: 20px;"
                row-key="id"
                :key="refreshTableData"
                ref="platRoleTreeTable"
                @select-all="tableTreeSelectAll"
                @select="tableTreeSingleSelect"
                @selection-change="tableTreeSelectChange"
                default-expand-all
                border
                :tree-props="{children: 'detail', hasChildren: 'hasChildren'}">
                <el-table-column
                  type="selection"
                  :selectable="isSelect"
                  width="50">
                </el-table-column>
                <el-table-column
                  prop="id"
                  label="ID"
                  v-if="clmShow">
                </el-table-column>
                <el-table-column
                  prop="caption"
                  label="权限名称"
                  >
                </el-table-column>
              </el-table>
            </el-container>
          </el-container>
        </el-card>
      </div>

      <div slot="footer" class="dialog-footer">
        <el-button @click="resetForm('thermalRoleForm1')">重 置</el-button>
        <el-button @click="closeForm()">取 消</el-button>
        <el-button type="primary" @click="submitForm('thermalRoleForm1')">确 定</el-button>
      </div>
    </el-dialog>

  </div>
</template>

<script>
export default {
  name: "RoleInfoThermal",
  data(){
    let Rcaption = (rule, value, callback) => {
      if (value.trim().length === 0) {
        callback(new Error('角色名称不可为空'));
      } else {
        callback();
      }
    };

    return {
      clmShow:false,
      ballShow:false,
      menuLeft: '-80',  //偏移量
      menuWidth: '80px',//菜单宽度   菜单宽度增加多少 偏移量同步增加多少
      floatDivWidth: 70, //悬浮窗宽度
      floatRightValue:'50px',  //悬浮窗位置右起
      floatTopValue:'55px',//悬浮窗位置上起
      ballIcon:'el-icon-more',
      cardBodyHeight:true,
      cardBodyStyle:'padding-top:10px;padding-bottom:10px;',
      icon1:'el-icon-caret-top',

      tableData:[],//表格数据
      tableTreeData:[],//菜单树数据
      treeDateForCheck:[],
      tableDataClone:[],
      multipleSelection: [],//table选中行内容
      sortOrderCol:'',
      menuNum:0,

      tableDialgTitle:'',//新增，修改
      tableOperFlag:'',//1是新增，2是修改，3是删除。
      permissionType :'all',
      refreshTableData:new Date().getTime(),
      tableRowIdMap:new Map(),
      tableCheckedKeys: false,
    /** 新增编辑表单参数 */
      thermalRoleForm:{
        r_id:'',
        r_caption:'',
        r_memo:'',
      },
     // orgIds:[], //选择的菜单id
      permissionFormRules:{
        caption: [
          {validator: Rcaption, trigger:'blur'}
        ],
        memo: [
          {max: 20,message:"字符与汉字不要超过20", trigger:'blur'}
        ]

      },
    /** 是否显示隐藏窗口 */
      thermalRoleFormVisible:false,
    /** 查询参数 */
      queryParam:{
        totalRow:0,
        currentPage:1,
        pageSize:this.customGlobal.pageSize,
      },

    /** 查询表单 */
      ruleForm: {
        r_caption: ''
      },
      rules: {
        r_caption: [
           {validator: Rcaption, trigger:'change'}
        ]
      },
    }

  },
  mounted() {
    // let that = this;
    // window.onresize= function temp() {
    //   that.clientHeight = (window.innerHeight-105);
    // };

  },
  created() {
   // this.initTableTreeData();
    this.queryValveCurrentData();

  },

  directives: {   //自定义函数书写位置
    trigger:{
      inserted(el, binging){
      //  console.log("inserted:"+el.id);
        if(el.id==='ballBtn'){
          el.click();
        }

      }
    },
    drag(el) {
      let oDiv = el;  // 当前元素
      let firstTime = "", lastTime = "";  //声明时间用来区分拖拽和单机
      document.onselectstart = function () {       // 禁止选择网页上的文字
        return false;
      }
      oDiv.onmousedown = function (e) {
        let disX = e.clientX - oDiv.offsetLeft          // 鼠标按下，计算当前元素距离可视区的距离    // 算出鼠标相对元素的位置     // 用鼠标的位置减去鼠标相对元素的位置，得到元素的位置
        let disY = e.clientY - oDiv.offsetTop
        document.getElementById("dragbtn").setAttribute("data-flag", false);
        document.getElementById("dragbtn").setAttribute("show2", oDiv.offsetLeft);
        firstTime = new Date().getTime();
        document.onmousemove = function (e) {
          let l = e.clientX - disX               // 用鼠标的位置减去鼠标相对元素的位置，得到元素的位置 左侧多少 ，不是定型尺寸哦 ，是定位尺寸
          let t = e.clientY - disY
          // console.log(e.clientX+"移动后的鼠标位置 ，已经和这个没移动前不一样了  ")
          if (l <= 0) {
            l = 0;
          } else if (l >= document.documentElement.clientWidth - oDiv.clientWidth) {
            //document.documentElement.clientWidth 屏幕的可视宽度
            l = document.documentElement.clientWidth - oDiv.clientWidth
          }
          document.getElementById("dragbtn").setAttribute("show2", l.toString()); //无法使用this，只好赋值给属性
          if (t <= 0) {
            t = 0;
          } else if (t >= document.documentElement.clientHeight - oDiv.clientHeight) {  //本身的宽高  oDiv.clientHeight
            t = document.documentElement.clientHeight - oDiv.clientHeight              // document.documentElement.clientHeight 屏幕的可视高度
          }
          oDiv.style.left = l + 'px'             // 移动当前元素
          oDiv.style.top = t + 'px'
        }
        document.onmouseup = function (e) {
          document.onmousemove = null;
          document.onmouseup = null;
          lastTime = new Date().getTime();
          if ((lastTime - firstTime) < 200) {
            document.getElementById("dragbtn").setAttribute("data-flag", true)
          }
        }
        return false          // return false不加的话可能导致黏连，就是拖到一个地方时div粘在鼠标上不下来，相当于onmouseup失效
      }
    }
  },

  methods:{
  /** 设置查询框隐藏显示  */
    confCardBodyHeigth(){
      if(this.cardBodyHeight===false){
        this.cardBodyHeight = true;
        this.cardBodyStyle = 'padding-top:10px;padding-bottom:10px;';
        this.icon1='el-icon-caret-top';
      }else{
        this.cardBodyHeight = false;
        this.cardBodyStyle = 'padding-top:0px;padding-bottom:0px;';
        this.icon1='el-icon-caret-bottom';
      }
    //  console.info("confCardBodyHeigth:"+this.cardBodyHeight);
    },
  /**  表格列排序设置   */
    sortColumn(column) {
    //  console.log("已经排序项：" + JSON.stringify(column));
      let orderFlag = '';
      if (column.order != null) {
        if (column.order === "descending") {
          orderFlag = 'desc';
        } else if (column.order === "ascending") {
          orderFlag = 'asc';
        }
      //  console.info("属性名称："+ column.prop+",排序方式："+column.order);

        this.sortOrderCol = column.prop + " " + orderFlag;
      //  console.info("用户选择排序列："+this.sortOrderCol);
        this.queryValveCurrentData ();
      }else{
        this.sortOrderCol = '';
      }

    },
  /**----分页方法-------start----*/
    handleSizeChange(val) {
    //  console.log(`每页 ${val} 条`);
      this.queryParam.pageSize = val;
      this.queryValveCurrentData();
    },
    handleCurrentChange(val) {
    //  console.log(`当前页: ${val}`);
      this.queryParam.currentPage = val ;
      this.queryValveCurrentData();
    },
  /**----分页方法--------end-----*/
   /* handleSelectionChange(val) {
      this.multipleSelection = val;
     // console.info("选择的行："+JSON.stringify( this.multipleSelection) );
    },*/
  /** 页面table复选框选中事件 */
    isSelect(row, index){
      return true;
    },
  /**  打开悬浮球   */
    openBox() {
      let isDrag = document.getElementById("dragbtn").getAttribute("data-flag");
      if(isDrag==true){
        return;
      }

      if(this.ballIcon==='el-icon-more'){
        this.ballIcon='el-icon-right';
      }else{
        this.ballIcon='el-icon-more';
      }

      var x = this.$refs['ballBtn'].$el.getBoundingClientRect().x;
      var w =  document.documentElement.clientWidth;
      //console.log('点击事件x:'+x+",w:"+w/2);
      this.ballShow = !this.ballShow;
      if(x>(w/2)){//大于窗口一半
        document.getElementById("buttonThermalUser").style.left = this.menuLeft + 'px';
      }else{//小于窗口一半
        document.getElementById("buttonThermalUser").style.left = Math.abs( this.menuLeft )  + 'px';// '70px';
      }
    },


  /** treeTable 全选方法 */
    tableTreeSelectAll() {//选择全部及不选择
      this.tableCheckedKeys = !this.tableCheckedKeys;

      this.spliteTable(this.tableTreeData, this.tableCheckedKeys);
      let table_arr =  this.$refs.platRoleTreeTable.selection;
    //  console.info("All方法选择的行："+JSON.stringify(table_arr) );

      if(table_arr.length===0){
        this.tableRowIdMap.clear();
      }else{
        for (let i = 0; i < table_arr.length; i++) {
          this.tableRowIdMap.set(table_arr[i].id, true);
        }
      }

    },
  /** 处理数据  */
    spliteTable(rows, flag) {
      for (let i = 0; i < rows.length; i++) {
       // console.info("spliteTable_i:"+i);
        this.$refs.platRoleTreeTable.toggleRowSelection(rows[i],flag);
        rows[i].status = flag;

        //this.tableRowIdMap.set(rows[i].id,flag===null?true:false);
        if(this.tableRowIdMap.has(rows[i].id)===false) {
          this.tableRowIdMap.set(rows[i].id, true);
        }else if(this.tableRowIdMap.get(rows[i].id)===true){
          // this.tableRowIdMap.set(rows[i].id,false);
          this.tableRowIdMap.delete(rows[i].id );
        }else {
          this.tableRowIdMap.set(rows[i].id,true,rows[i].caption);
        }

        if(rows[i].detail){
          this.spliteTable(rows[i].detail,flag);
        }

      }
    //  console.info("spliteTable_tableRowIdMap:"+JSON.stringify(this.tableRowIdMap));
    },
  /** 点击当前行checkbox事件 （select事件） */
    tableTreeSingleSelect(selection, row){
      if(this.tableRowIdMap.has(row.id)===false) {
        this.tableRowIdMap.set(row.id, true);
      }else if(this.tableRowIdMap.get(row.id)===true){
        // this.tableRowIdMap.set(row.id,false);
        this.tableRowIdMap.delete(row.id );
      }else {
        this.tableRowIdMap.set(row.id,true);
      }
    //  console.info("tableRowIdMap:"+JSON.stringify(this.tableRowIdMap));
    //  console.info("tableSingleSelect_row:"+JSON.stringify( row ) );
      if(row.detail){
        this.spliteTable(row.detail);
      }
    },
  /**  选择当前行  */
    handleSelectTreeCurrent(row){
    //  console.info("handleSelectCurrent_row.id:"+row.id);
      if(row.status==='false'){
        row.status = 'true';
      }else  if(row.status==='true'){
        row.status = 'false';
      }

      if(row.detail!=undefined){
        this.spliteTable(row.detail);
      }

      this.$refs.platRoleTreeTable.toggleRowSelection(row);

      if(this.tableRowIdMap.has(row.id)===false) {
        this.tableRowIdMap.set(row.id, true);
      }else if(this.tableRowIdMap.get(row.id)===true){
        // this.tableRowIdMap.set(row.id,false);
        this.tableRowIdMap.delete(row.id );
      }else {
        this.tableRowIdMap.set(row.id,true);
      }
    },
  /** 权限树初始化 */
    initTableTreeData(){
      let that = this;
      let _jsondata_ =   {
        config:this.permissionType//platfromconfig,heatconfig,all
      } ;
      const loading = this.$loading({
        lock: true,
        text: '查询中...',
        target: '.main',
        spinner: 'el-icon-loading',
        background: 'rgba(0, 0, 0, .7)'
      });
      var p1 = this.customGlobal.postHttpClient(
        this.customGlobal.serverUrl.postQueryPermissionTemplate,
        //this.customGlobal.serverUrl.baseURI,
        this.customGlobal.serverUrl.orgBaseURI,
        {"Content-Type": "application/x-www-form-urlencoded"},
        this.qs.stringify({jsondata:JSON.stringify(_jsondata_)})
      );
      p1.then((res) => {
      //  console.log("异步调用成功返回:"+JSON.stringify(res));
        if(res.data.status=='success'){
          loading.close();
          let permission = JSON.parse( res.data.permission);
          if(this.permissionType==='all'){
            that.tableTreeData = [
              {
                caption:'银行用户角色权限',
                id:'00-0',//特殊标识
                detail:permission.heatconfig.permissionconfig.detail
              }
            ];
          }else{
            that.tableTreeData = permission.permissionconfig.detail;
          }
          that.tableDataClone = that.tableTreeData;
        }else{
        //  console.info("查询失败");
          loading.close();
          that.tableTreeData = [];
          // this.$alert('登录已超时，请重新登录！', '', {
          //   confirmButtonText: '确定',
          //   callback: action => {
          //   //  console.info('data:'+JSON.stringify(res.data));
          //     if(res.data.msg==='请登录认证'){
          //       this.$router.push({path:'/login'});
          //     }
          //   }
          // });
          if(res.data.msg==='请登录认证'){
            this.$router.push({path:'/login'});
          }
        }
      },error=>{
      //  console.log("异步调用出错:"+JSON.stringify(error));
        loading.close();
        that.tableTreeData = [];
        this.$alert(JSON.stringify(error.message), '查询出错!', {
          confirmButtonText: '确定',
          callback: action => {
          }
        });
      }).catch((res) => {
      //  console.log("异步调用失败返回:"+JSON.stringify(res));
        loading.close();
        that.tableTreeData = [];
        this.$alert(JSON.stringify(res.message), '查询出错', {
          confirmButtonText: '确定',
          callback: action => {
          }
        });
      });
    },
  /** 当选项发生改变时触发 */
    tableTreeSelectChange(val) {
      this.multipleSelection = val;
      this.menuNum=this.tableRowIdMap.size;
    //  console.info("change-选择的菜单树行："+JSON.stringify( this.multipleSelection) );
    //  console.log("-change-选择的所有ID-"+JSON.stringify(this.tableRowIdMap))
    },
  /** 重新渲染treeTable */
    initLoopCheckTable(rows){//循环检测选中状态
      for (let i = 0; i < rows.length; i++) {
      //  console.info("initLoopCheckTable_i:"+i+",id:"+rows[i].id+",status:"+rows[i].status);
        if(rows[i].status==='false'){
          // this.tableRowIdMap.set(rows[i].id,false);
          this.tableRowIdMap.delete(rows[i].id );
          this.$refs.platRoleTreeTable.toggleRowSelection(rows[i],false)
        }else if(rows[i].status==='true'){
          this.tableRowIdMap.set(rows[i].id,true);

          this.$refs.platRoleTreeTable.toggleRowSelection(rows[i],true)
        }
        if(rows[i].detail){
          this.initLoopCheckTable(rows[i].detail);
        }

      }

    },
    /***
     * 增加同级或子级
     * @param curId 当前选中的节点ID
     * @param sourceData tableData数据对象
     * @param targetData 新增加或修改的对象内容
     * @param layer 1是同级，2是子级
     * @param flag 1是新增，2是修改，3是删除,4是查询。5是将status修改成true
     *
     */
    checkPermissionId(curId,sourceData,targetData,layer,flag){
    //  console.info("进入checkPermissionId:"+sourceData)
    //  console.info("查询选中ID:"+curId);
      var result = false;
      for(var i1=0;i1<sourceData.length;i1++){
      //  console.info("当前id:"+sourceData[i1].id);
        if(flag==='1'){
          if(sourceData[i1].id===curId ){
            if(layer==='1'){//同级
              sourceData.push(targetData);
              this.tableDataClone = this.tableData;
            //  console.info("add find ok");
              result = true;
              break;
            }else if (layer==='2'){
              if(sourceData[i1].hasOwnProperty("detail")){
              //  console.info("add 存在detail,直接追加:"+JSON.stringify(sourceData[i1]))
                let detail = sourceData[i1].detail;

                detail.push(targetData);
                sourceData[i1].detail = detail;
                this.tableDataClone = this.tableData;
                result = true;
                break;
              }else{
                let detail = [];

                detail.push(targetData);
                sourceData[i1].detail = detail ;
              //  console.info("add 不存在detail,新创建detail:"+JSON.stringify(sourceData[i1].detail));
                this.tableDataClone = this.tableData;
                result = true;
                break;
              }
            }
            //break;
          } else{
            if(sourceData[i1].hasOwnProperty("detail")){
            //  console.info("add not find 递归查询detail")
              result = this.checkPermissionId(curId,sourceData[i1].detail,targetData,layer,flag);
              if(result===true){
                break;
              }
            }else{
            //  console.info("add not find");
              result = false;
            }
          }

        }//end if(flag==='1'){
        else  if(flag==='2'){
          if(sourceData[i1].id===curId ){
            sourceData[i1].caption = targetData.caption;
            sourceData[i1].vue = targetData.vue;
            sourceData[i1].url = targetData.url;
            sourceData[i1].icon = targetData.icon;
          //  console.info("modify find ok");
            this.tableDataClone = this.tableData;
            result = true;
            break;
          }else{
            if(sourceData[i1].hasOwnProperty("detail")){
            //  console.info("modify not find 递归查询detail")
              result = this.checkPermissionId(curId,sourceData[i1].detail,targetData,layer,flag);
              if(result===true){
                break;
              }
            }else{
            //  console.info("modify not find");
              result = false;
            }
          }
        }else if(flag==='3'){
          if(sourceData[i1].id===curId ){
            sourceData.splice(i1, 1);
            this.tableDataClone = this.tableData;
          //  console.info("del find ok");
            result = true;
            break;
          }else{
            if(sourceData[i1].hasOwnProperty("detail")){
            //  console.info("del not find 递归查询detail")
              result = this.checkPermissionId(curId,sourceData[i1].detail,targetData,layer,flag);
              if(result===true){
                break;
              }
            }else{
            //  console.info("del not find");
              result = false;
            }
          }
        }else if(flag==='4'){
       //   console.info('查询表格内容');
          //if(sourceData[i1].caption.toLowerCase().includes(curId.toLowerCase())){
        //  console.info("query find indexOf:"+sourceData[i1].caption.toLowerCase().indexOf(curId.toLowerCase()));
          if(sourceData[i1].caption.toLowerCase().indexOf(curId.toLowerCase())>-1){
            let newdata = [];
            newdata.push(sourceData[i1]);
            this.tableDataClone = newdata;
          //  console.info("query find ok."+ sourceData[i1].caption);
            result = true;
            break;
          }else{
            if(sourceData[i1].hasOwnProperty("detail")){
            //  console.info("query not find 递归查询detail")
              result = this.checkPermissionId(curId,sourceData[i1].detail,targetData,layer,flag);
              if(result===true){
                break;
              }
            }else{
            //  console.info("query not find");
              this.tableDataClone = [];
              result = false;

            }
          }
        }else if(flag==='5'){
        //  console.info('查询表格status内容');
          //if(sourceData[i1].caption.toLowerCase().includes(curId.toLowerCase())){
        //  console.info("query find status id:"+sourceData[i1].id);
          if(sourceData[i1].id===curId){
            sourceData[i1].status = 'true';
          //  console.info("modify status find ok");
            this.tableDataClone = this.tableData;
          //  console.info("query find status ok id:"+ sourceData[i1].id);
            result = true;
            break;
          }else{
            if(sourceData[i1].hasOwnProperty("detail")){
            //  console.info("query status not find 递归查询detail")
              result = this.checkPermissionId(curId,sourceData[i1].detail,targetData,layer,flag);
              if(result===true){
                break;
              }
            }else{
            //  console.info("query status not find");
              this.tableDataClone = [];
              result = false;

            }
          }
        }


      }
      return result;

    },
  /** 清空treeTable的复选框 */
    noCheckTreeTable(rows){//循环检测选中状态
      this.tableRowIdMap=new Map();
      for (let i = 0; i < rows.length; i++) {
        this.$refs.platRoleTreeTable.toggleRowSelection(rows[i],false)
        rows[i].status = false;
        if(rows[i].detail){
          this.noCheckTreeTable(rows[i].detail);
        }
      }
    },
  /** 用于权限树编辑时回显用 目前仅能做到三级回显 (已弃用)*/
    checkedTreeTable(orgIds){
      this.$nextTick(() => {
        this.tableTreeData.forEach(outerItem => {
          // 判断第一层是否有子级
          if (outerItem.detail != '') {
            outerItem.detail.forEach(outerItem1 => {
              //判断第二层是否有子级
              for (let i = 0; i < orgIds.length; i++) {
                if (outerItem1.id === orgIds[i]) {
                  this.$refs.platOrgTreeTable.toggleRowSelection(outerItem1, true);
                  //  console.log(outerItem1.caption + "在第二层选中了----：" + outerItem1.caption)
                }
              }
              if (outerItem1.detail != '') {
                outerItem1.detail.forEach(outerItem2 => {
                  //判断treeTable第三层是否有选中的
                  for (let i = 0; i < orgIds.length; i++) {
                    if (outerItem2.id === orgIds[i]) {
                      this.$refs.platRoleTreeTable.toggleRowSelection(outerItem2, true);
                    //  console.log(outerItem1.caption + "在第三层选中了----：" + outerItem2.caption)
                    }
                  }
                })
              }
            })
          }
        })
      })
    },
  /** 将用户权限点赋予到tableData */
    initTableDataPermissionStatus(permissionKey,tableData){
      //let permissionkey={"2":true,"3":true,"UUID_20012001_20012001_001":true,"UUID_10011001_10011001_002":true,"4":true,"UUID_4_1":true};
      for (let k of Object.keys(permissionKey)) {
        this.checkPermissionId(k,tableData,
          {},
          '0',
          '5');
      }
      //初始化渲染
      this.$nextTick(() => {
        this.initLoopCheckTable(tableData);
      })
    },


  /** 获取用户权限ID */
    getPermiessionId(roleid){
      let _jsondata_ =   {
        role_id:roleid
      } ;
      var that = this;
      //此方法需要4个参数：方法地址、服务器地址、post头参数、传递的参数
      var p1 = this.customGlobal.postHttpClient(
        this.customGlobal.serverUrl.platformRole_queryRoleByIds,// 方法路径
        this.customGlobal.serverUrl.orgBaseURI,//服务器地址
        {"Content-Type": "application/x-www-form-urlencoded"},
        this.qs.stringify({jsondata:JSON.stringify(_jsondata_)})
      );
      //post提交后返回值操作
      p1.then((res) => {
        //  console.log("异步调用成功返回:"+JSON.stringify(res));
        if(res.data.status=='success') {
          let jsonObj=res.data.rows;
          let roleIds= {};
          //将查询出来的权限ID分别赋值给tableRowIdMap以及一个用于回显的自定义数组
          if(jsonObj.length>0){
            for(let item in jsonObj) {
              this.tableRowIdMap.set(jsonObj[item].rp_p_id, true);
              roleIds[jsonObj[item].rp_p_id]=true;
            }
            this.menuNum=jsonObj.length;
          }
          //先清空再回显
          this.noCheckTreeTable(that.tableTreeData)
          this.initTableDataPermissionStatus(roleIds,that.tableTreeData);
        }else{
        //  console.info("查询失败");
          // loading.close();
          that.tableTreeData = [];
          // this.$alert('登录已超时，请重新登录！', '', {
          //   confirmButtonText: '确定',
          //   callback: action => {
          //  //   console.info('data:'+JSON.stringify(res.data));
          //     if(res.data.msg==='请登录认证' && res.data.status==='failed'){
          //       this.$router.replace({path:'/login'});
          //     }
          //   }
          // });
          if(res.data.msg==='请登录认证' && res.data.status==='failed'){
            this.$router.replace({path:'/login'});
          }
        }
      },error=>{
      //  console.log("异步调用出错:"+JSON.stringify(error));
        // loading.close();
        that.tableTreeData = [];
        this.$alert(JSON.stringify(error.message), '查询出错!', {
          confirmButtonText: '确定',
          callback: action => {

          }
        });
      }).catch((res) => {
      //  console.log("异步调用失败返回:"+JSON.stringify(res));
        that.tableTreeData = [];
        this.$alert(JSON.stringify(res.message), '查询出错', {
          confirmButtonText: '确定',
          callback: action => {

          }
        });
      });
    },
    reQueryValveCurrentData (){
      this.queryParam.currentPage = 1;
      this.queryValveCurrentData ();
    },
  /** 查询方法 */
    queryValveCurrentData (){
      let that = this;
      let _jsondata_ =   {
        r_bank_center_id: that.customGlobal.userInfoExt.cui_bank_center_id,
        r_province_id: that.customGlobal.userInfoExt.cui_province_id,
        r_city_id: that.customGlobal.userInfoExt.cui_city_id,
        r_county_id: that.customGlobal.userInfoExt.cui_county_id,
        r_station_id: that.customGlobal.userInfoExt.cui_station_id,
        perPageSize:this.queryParam.pageSize,
        currentPage:this.queryParam.currentPage,
        r_caption:this.ruleForm.r_caption,
      } ;
      if(this.sortOrderCol!==''){
        _jsondata_.orderby = this.sortOrderCol;
      }else{
        _jsondata_.orderby=' r_writetime desc ';
      }
    //  console.info(JSON.stringify(_jsondata_));
      const loading = this.$loading({
        lock: true,
        text: '查询中...',
        target: '.main',
        spinner: 'el-icon-loading',
        background: 'rgba(0, 0, 0, .7)'
      });

      //此方法需要4个参数：方法地址、服务器地址、post头参数、传递的参数
      var p1 = this.customGlobal.postHttpClient(
        this.customGlobal.serverUrl.thermalRole_query,// 方法路径
        this.customGlobal.serverUrl.orgBaseURI,//服务器地址
        {"Content-Type": "application/x-www-form-urlencoded"},
        this.qs.stringify({jsondata:JSON.stringify(_jsondata_)})
      );
      //post提交后返回值操作
      p1.then((res) => {
        // console.log("异步调用成功返回:"+JSON.stringify(res));
        if(res.data.status=='success'){

          loading.close();
          that.tableData = res.data.rows;
          that.queryParam.totalRow = res.data.total;

        }else{
        //  console.info("查询失败");
          loading.close();
          that.tableData = [];
          // this.$alert('登录已超时，请重新登录！', '', {
          //   confirmButtonText: '确定',
          //   callback: action => {
          //   //  console.info('data:'+JSON.stringify(res.data));
          //     if(res.data.msg==='请登录认证' && res.data.status==='failed'){
          //       this.$router.replace({path:'/login'});
          //     }
          //   }
          // });
          if(res.data.msg==='请登录认证' && res.data.status==='failed'){
            this.$router.replace({path:'/login'});
          }
        }
      },error=>{
      //  console.log("异步调用出错:"+JSON.stringify(error));
        loading.close();
        that.tableData = [];
        this.$alert(JSON.stringify(error.message), '查询出错!', {
          confirmButtonText: '确定',
          callback: action => {

          }
        });
      }).catch((res) => {
      //  console.log("异步调用失败返回:"+JSON.stringify(res));
        loading.close();

        that.tableData = [];
        this.$alert(JSON.stringify(res.message), '查询出错', {
          confirmButtonText: '确定',
          callback: action => {

          }
        });
      });

    },
  /** 银行用户角色新增 */
    platformAddRole(){
      this.initTableTreeData();
      this.thermalRoleForm.r_caption='';
      this.thermalRoleForm.r_memo='';
      this.tableRowIdMap=new Map();
      this.thermalRoleFormVisible=true;
      this.tableDialgTitle="新增银行用户角色";
      this.tableOperFlag=1;
    },
  /** 银行用户角色编辑 */
    platformEditRole(row){

      this.initTableTreeData();
      this.tableRowIdMap=new Map();
      this.thermalRoleFormVisible=true;
      this.tableDialgTitle="编辑银行用户角色["+row.r_caption+"]";
      this.tableOperFlag=2;
      this.thermalRoleForm.r_id=row.r_uuid;
      this.thermalRoleForm.r_caption=row.r_caption;
      this.thermalRoleForm.r_memo=row.r_memo;
      this.getPermiessionId(row.r_uuid);

    },
  /** 银行用户角色删除 */
    platformDelRole(row){
      this.tableOperFlag=3;

      this.$confirm('请再次确认是否要删除角色[<span style="color:red;">'+row.r_caption+'</span>]的记录?', '删除提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning',
        dangerouslyUseHTMLString:true,
        closeOnClickModal:false,
        closeOnPressEscape:false,

      }).then(() => {

        let _jsondata_ =   {
          uid:row.r_uuid
        } ;
        const loading = this.$loading({
          lock: true,
          text: '操作中...',
          target: '.main',
          spinner: 'el-icon-loading',
          background: 'rgba(0, 0, 0, .7)'
        });
        //此方法需要4个参数：方法地址、服务器地址、post头参数、传递的参数
        var p1 = this.customGlobal.postHttpClient(
          this.customGlobal.serverUrl.thermalRole_delete,// 方法路径
          this.customGlobal.serverUrl.orgBaseURI,//服务器地址
          {"Content-Type": "application/x-www-form-urlencoded"},
          this.qs.stringify({jsondata:JSON.stringify(_jsondata_)})
        );
        //post提交后返回值操作
        p1.then((res) => {
          //  console.log("异步调用成功返回:"+JSON.stringify(res));
          this.queryValveCurrentData();
          loading.close();
          if(res.data.status=='success'){
            this.$message({
              type: 'success',
              message: '删除成功!'
            });
          }else{
          //  console.info("删除失败");
            loading.close();
            // this.$alert('登录已超时，请重新登录！', '', {
            //   confirmButtonText: '确定',
            //   callback: action => {
            //   //  console.info('data:'+JSON.stringify(res.data));
            //     if(res.data.msg==='请登录认证' && res.data.status==='failed'){
            //       this.$router.replace({path:'/login'});
            //     }
            //   }
            // });
            if(res.data.msg==='请登录认证' && res.data.status==='failed'){
              this.$router.replace({path:'/login'});
            }
          }
        },error=>{
        //  console.log("异步调用出错:"+JSON.stringify(error));
          loading.close();
          this.$alert(JSON.stringify(error.message), '删除出错!', {
            confirmButtonText: '确定',
            callback: action => {

            }
          });
        }).catch((res) => {
        //  console.log("异步调用失败返回:"+JSON.stringify(res));
          loading.close();

          this.$alert(JSON.stringify(res.message), '删除出错', {
            confirmButtonText: '确定',
            callback: action => {

            }
          });
        });

      }).catch(() => {
        loading.close();
        this.$message({
          type: 'info',
          message: '已取消删除'
        });
      });

    },
  /** 重置表单 */
    resetForm(formName) {
      this.$nextTick(() => {
        this.$refs[formName].resetFields();
      })
    },
  /** 关闭表单 */
    closeForm(){
      this.thermalRoleFormVisible = false;
      this.$refs['thermalRoleForm1'].clearValidate();
    },
  /** 表单提交 */
    submitForm(formName) {
      let url_method='';
    let msg_show='';
      let flag=this.tableOperFlag;
      switch (flag) {
        case 1:
          url_method=this.customGlobal.serverUrl.thermalRole_add;
          msg_show="添加";
          break;
        case 2:
          url_method=this.customGlobal.serverUrl.thermalRole_update;
          msg_show="编辑";
          break;
      }
      //将存储RoleID的map转换为数组
      var arrRole=new Array();
      let i=0;
      this.tableRowIdMap.forEach(function(value,key){
        arrRole[i]=key;
        i++;
      });
      if(arrRole.length==0){
        this.$alert('您还未选择菜单信息，不能提交数据！', '提示信息', {
          confirmButtonText: '确定',
          callback: action => {
          }
        });
        return;
      }
      this.$refs[formName].validate((valid) => {

        if (valid) {
          let _jsondata_ =   {
            r_bank_center_id: this.customGlobal.userInfoExt.cui_bank_center_id,
            r_province_id: this.customGlobal.userInfoExt.cui_province_id,
            r_city_id: this.customGlobal.userInfoExt.cui_city_id,
            r_county_id: this.customGlobal.userInfoExt.cui_county_id,
            r_station_id: this.customGlobal.userInfoExt.cui_station_id,
            id:this.thermalRoleForm.r_id,
            caption:this.thermalRoleForm.r_caption,
            memo:this.thermalRoleForm.r_memo,
            plat_org:arrRole
          } ;
          const loading = this.$loading({
            lock: true,
            text: '提交中...',
            target: '.main',
            spinner: 'el-icon-loading',
            background: 'rgba(0, 0, 0, .7)'
          });

          //此方法需要4个参数：方法地址、服务器地址、post头参数、传递的参数
          var p1 = this.customGlobal.postHttpClient(
            url_method,// 方法路径
            this.customGlobal.serverUrl.orgBaseURI,//服务器地址
            {"Content-Type": "application/x-www-form-urlencoded"},
            this.qs.stringify({jsondata:JSON.stringify(_jsondata_)})
          );
          //post提交后返回值操作
          p1.then((res) => {
            //  console.log("异步调用成功返回:"+JSON.stringify(res));
            if(res.data.status=='success'){
              this.thermalRoleFormVisible=false;
              this.queryValveCurrentData();
              this.platformUserFormVisible=false;
              loading.close();
              this.$message({
                type: 'success',
                message: msg_show+'成功!'
              });
            }else{
            //  console.info("提交失败");
              loading.close();
              // this.$alert('登录已超时，请重新登录！', '', {
              //   confirmButtonText: '确定',
              //   callback: action => {
              //   //  console.info('data:'+JSON.stringify(res.data));
              //     if(res.data.msg==='请登录认证' && res.data.status==='failed'){
              //       this.$router.replace({path:'/login'});
              //     }
              //   }
              // });
              if(res.data.msg==='请登录认证' && res.data.status==='failed'){
                this.$router.replace({path:'/login'});
              }
            }
          },error=>{
          //  console.log("异步调用出错:"+JSON.stringify(error));
            loading.close();
            this.$alert(JSON.stringify(error.message), '提交出错!', {
              confirmButtonText: '确定',
              callback: action => {

              }
            });
          }).catch((res) => {
          //  console.log("异步调用失败返回:"+JSON.stringify(res));
            loading.close();

            this.$alert(JSON.stringify(res.message), '提交出错', {
              confirmButtonText: '确定',
              callback: action => {

              }
            });
          });

        } else {
        //  console.log('error submit!!');
          return false;
        }
      });
    },
  },

}
</script>

<style scoped>
/*<style >*/

.thermal-row-card1 /deep/ .el-card__header{
  /*background:#f5f5f5 !important;*/
  background:#f2f3f8 !important;
  /*height: 10px!important;*/
  padding:10px!important;
}
.thermal-row-card2 /deep/ .el-card__body{
  padding:5px 20px 5px 20px !important;

}
.thermal-row-formitem /deep/ .el-form-item {margin-bottom: 3px!important;}

.thermal-row-dialog /deep/ .el-dialog__body{
  padding: 0px 20px!important;
  color: #606266!important;
  font-size: 14px!important;
  word-break: break-all!important;
  margin-left: 0px!important;
}


</style>



